}

a {
  text-decoration: none;
}

button {
  background-color: transparent;
  line-height: 1;
  padding: 0;
  border: solid 0.1rem black;
  border: none;
  cursor: pointer;
}

.header {
  background: rgb(255, 143, 112);
  background: linear-gradient(90deg, rgb(255, 143, 112) 0%, rgb(255, 61, 83) 100%);
  border-radius: 0 0 0 7.5rem;
  max-width: 144rem;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    max-width: 144rem;
    margin: 0 auto;
  }
}
.header::before {
  content: "";
  position: absolute;
  background-image: url("../img/bg-pattern-intro-mobile.svg");
  background-repeat: no-repeat;
  background-position: -32.5rem -32.5rem;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 0 0 0 7.5rem;
}
@media (min-width: 768px) {
  .header::before {
    background-image: url("../img/bg-pattern-intro-desktop.svg");
    background-position: -100rem -125rem;
  }
}
@media (min-width: 992px) {
  .header::before {
    background-position: -85rem -130rem;
  }
}
@media (min-width: 1200px) {
  .header::before {
    background-position: -68rem -133rem;
  }
}
@media (min-width: 1440px) {
  .header::before {
    background-position: -45rem -131rem;
  }
}
.header__container {
  position: relative;
  padding: 5.5rem 2.5rem 15.5rem;
}
@media (min-width: 768px) {
  .header__container {
    padding: 6rem 2.5rem 15rem;
  }
}
@media (min-width: 992px) {
  .header__container {
    padding: 6rem 10rem 15rem;
  }
}
@media (min-width: 1200px) {
  .header__container {
    padding: 6rem 16rem 15rem;
  }
}
.header__navegation {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
@media (min-width: 768px) {
  .header__navegation {
    flex-wrap: nowrap;
    justify-content: flex-start;
    gap: 2.5rem;
  }
}
@media (min-width: 992px) {
  .header__navegation {
    gap: 6.5rem;
  }
}
.header__logo {
  height: 3rem;
  width: 8.5rem;
  object-fit: contain;
}
@media (min-width: 768px) {
  .header__logo {
    height: 5rem;
    width: 10rem;
  }
}
.header__button {
  border: none;
}
@media (min-width: 768px) {
  .header__button {
    display: none;
  }
}
.header__icon {
  width: 3.5rem;
  height: 3rem;
  object-fit: contain;
}
.header__content {
  padding: 5rem 1.5rem 0;
}
@media (min-width: 768px) {
  .header__content {
    padding: 14rem 6rem 0;
  }
}
@media (min-width: 1200px) {
  .header__content {
    padding: 14rem 3rem 0;
  }
}
.header__heading {
  color: hsl(0deg, 0%, 100%);
  text-align: center;
  margin: 0;
}
@media (min-width: 768px) {
  .header__heading {
    font-size: 4.4rem;
  }
}
@media (min-width: 1200px) {
  .header__heading {
    font-size: 6rem;
  }
}
.header__text {
  text-align: center;
  color: hsl(0deg, 0%, 100%);
  line-height: 1.5;
  margin: 2.5rem 0;
}
@media (min-width: 1200px) {
  .header__text {
    font-size: 2rem;
  }
}
.header__information {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}
@media (min-width: 768px) {
  .header__information {
    gap: 2rem;
  }
}
.header__link {
  padding: 1.5rem 2rem;
  border-radius: 10rem;
  border: solid 0.1rem hsl(0deg, 0%, 100%);
  font-weight: 700;
  margin-top: 2.5rem;
}
@media (min-width: 768px) {
  .header__link {
    margin-top: 2rem;
  }
}
@media (min-width: 992px) {
  .header__link {
    margin-top: 2.5rem;
  }
}
.header__link:first-of-type {
  color: hsl(356deg, 100%, 66%);
  background-color: hsl(0deg, 0%, 100%);
}
@media (min-width: 768px) {
  .header__link:first-of-type {
    transition: color 350ms ease, background-color 350ms ease, border-color 350ms ease;
  }
}
@media (min-width: 768px) {
  .header__link:first-of-type:hover {
    color: hsl(0deg, 0%, 100%);
    background-color: hsl(356deg, 100%, 66%);
    border-color: hsl(356deg, 100%, 66%);
  }
}
.header__link:last-of-type {
  color: hsl(0deg, 0%, 100%);
  background-color: transparent;
}
@media (min-width: 768px) {
  .header__link:last-of-type {
    padding: 1.5rem 2.5rem;
    transition: color 350ms ease, background-color 350ms ease;
  }
}
@media (min-width: 768px) {
  .header__link:last-of-type:hover {
    background-color: hsl(0deg, 0%, 100%);
    color: hsl(356deg, 100%, 66%);
  }
}

.menu {
  position: absolute;
  top: 0;
  width: 100%;
  margin-top: 5rem;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 1rem;
  height: 0;
  pointer-events: none;
  overflow: hidden;
  box-shadow: 2px 0px 12px -2px rgba(0, 0, 0, 0.5);
  transition: all 500ms ease;
}
@media (min-width: 768px) {
  .menu {
    position: relative;
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: unset;
    pointer-events: auto;
    margin: 0;
    background-color: transparent;
    box-shadow: none;
    overflow: visible;
  }
}
.menu__boxes {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .menu__boxes {
    flex-direction: row;
    gap: 3rem;
  }
}
@media (min-width: 768px) {
  .menu__box {
    position: relative;
  }
}
.menu__box, .menu__links {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.menu__button {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0 1rem 2rem;
  color: hsl(208deg, 49%, 24%);
  font-weight: 500;
}
@media (min-width: 768px) {
  .menu__button {
    color: hsl(0deg, 0%, 100%);
    padding: 0;
  }
}
.menu__button:hover {
  text-decoration: underline;
}
.menu__arrow {
  transition: transform 350ms ease;
}
.menu__links {
  flex-direction: column;
  gap: 2.5rem;
  width: 100%;
  border-radius: 0.5rem;
  height: 0;
  overflow: hidden;
  pointer-events: none;
  transition: all 350ms ease;
  background-color: #efeff0;
}
@media (min-width: 768px) {
  .menu__links {
    position: absolute;
    align-items: flex-start;
    gap: 2rem;
    width: 11rem;
    top: 3rem;
  }
}
.menu__link {
  color: hsl(207deg, 13%, 34%);
  font-weight: 500;
}
@media (min-width: 768px) {
  .menu__link {
    font-weight: lighter;
    transition: font-weight 300ms ease;
  }
}
.menu__link:hover {
  font-weight: bolder;
}
.menu__subscribe {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  margin-top: 2.5rem;
  padding: 1.5rem 0 0;
  border-top: solid 0.1rem hsl(240deg, 2%, 79%);
}
@media (min-width: 768px) {
  .menu__subscribe {
    flex-direction: row;
    gap: 3rem;
    border: none;
    margin: 0;
    padding: 0;
  }
}
.menu__register:first-of-type {
  padding: 1.5rem 0;
}
@media (min-width: 768px) {
  .menu__register:first-of-type {
    color: hsl(0deg, 0%, 100%);
  }
  .menu__register:first-of-type:hover {
    font-weight: unset;
    text-decoration: underline;
  }
}
.menu__register:last-of-type {
  color: hsl(0deg, 0%, 100%);
  background: rgb(255, 143, 112);
  background: linear-gradient(90deg, rgb(255, 143, 112) 0%, rgb(255, 61, 83) 100%);
  padding: 1.5rem 4rem;
  border-radius: 10rem;
}
@media (min-width: 768px) {
  .menu__register:last-of-type {
    color: hsl(356deg, 100%, 66%);
    background: none;
    background-color: hsl(0deg, 0%, 100%);
    font-weight: bolder;
    transition: color 350ms ease, background-color 350ms ease;
  }
  .menu__register:last-of-type:hover {
    color: hsl(0deg, 0%, 100%);
    background-color: hsl(356deg, 100%, 66%);
  }
}

